body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root{
  margin: 0;
  padding: 0;

  height: 100%;
  width: 100%;

  overflow: hidden;
}

.Root {
  position: absolute;
  top: 0;
  left: 0;

  margin: 0;
  padding: 0;

  height: 100%;
  width: 100%;
  overflow: hidden;
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    border-radius: 5px;   
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: #0074d9;
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #0074d9;
    cursor: pointer;
}

.icons{
  font-family: 'Tonicons';
}

.word-break{
  word-wrap: break-word;
}

.bold{
  font-weight: bold;
}

.very-very-small{
  font-size: 0.75em;
}

.very-small{
  font-size: 0.8em;
}

.small{
  font-size: 0.9em;
}

.big{
  font-size: 1.5em;
}

.very-big{
  font-size: 2em;
}

.very-very-big{
  font-size: 3em;
}

.clear{
  clear: both;
}

.inline-block{
  display: inline-block;
}

.vertical-top{
  vertical-align: top;
}

.col{
  text-align: center;
}

.align-center{
  text-align: center;
}

.collapsed{
  overflow:hidden;
  transition:max-height 2s ease-in-out;
  height:auto;
  max-height:0;
}

.very-very-small-margin{
  margin: 0.75rem;
}

.very-small-margin{
  margin: 0.8rem;
}

.small-margin{
  margin: 0.9rem;
}

.outline{
  background: none;
}
  .button.outline{
    border: 1px solid #0074d9;
    color: #0074d9;
  }
  .button.outline:hover{
    border: 1px solid #0074d9;
    color: #0074d9;
  }

.page{
    position: absolute;
    top: 0;
    left: 0;

    padding: 1rem;
    padding-bottom: 3rem;
    padding-top: 1rem;

  display: block;
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  overflow: auto;

  -webkit-transform: translateX(-100%);

          transform: translateX(-100%);

  background-color: #efefef;
}

.page.active{
  -webkit-animation-name: slideInRight;
    animation-name: slideInRight;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes slideInRight {
  0% {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    visibility: visible;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  0% {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    visibility: visible;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
} 
